@import url(https://fonts.googleapis.com/css2?family=Maven+Pro:wght@400;500;600;700;800;900&display=swap);
body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
html, body {
  height: 100vh;
  overflow: hidden;
}
* {
  margin: 0;
  padding: 0;
}
html {
  scroll-behavior: smooth;
}
a {
  text-decoration: none;
  border: 0;
  display: flex;
  justify-content: center;
  align-content: center;
}
.container {
  height: 100vh;
  overflow-y: auto;
  overflow-x: hidden;
  scroll-behavior: smooth;
  -ms-scroll-snap-type: y mandatory;
      scroll-snap-type: y mandatory;
}
section {
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  flex-direction: column;
  width: 100%;
  height: 100%;
  scroll-snap-align: center;
  scroll-snap-stop: always;
}
#LandingPage {
  background-image: url(/static/media/northern-lights-3273425.d55c5a7a.jpg);
  background-size: cover;
}
.landing-flex-grid {
  margin: 50px;
  display: grid;
  grid-template-columns: 4fr 1fr 5fr;
  grid-template-rows: 50% 50%;

}
.profile-picture {
  border-radius: 50%;
  max-width: 25vw;
  height: auto;
  grid-column-start: 1;
  grid-row: 1 / 3;
  justify-self: center;
  align-self: center;
}
h1 {
  font-family: "Maven Pro", sans-serif;
  font-size: 40px;
  color: white;
}
h2 {
  font-family: "Maven Pro", sans-serif;
  font-size: 30px;
  color: #013535;
}
.name {
  font-weight: 500;
  font-size: 7vw;
  grid-column-start: 3;
  grid-row-start: 1;
  justify-self: center;
  align-self: center;
}
.occupation {
  font-weight: 400;
  font-size: 3.5vw;
  grid-column-start: 3;
  grid-row-start: 2;
  justify-self: center;
  align-self: center;
}

.animation-container {
  position: relative;
  width: 100%;
  height: 100%;
}
.greeting-container {
  position: absolute;
  display: flex;
  width: 100%;
  justify-content: center;
  -webkit-animation: rise2 15s ease forwards;
          animation: rise2 15s ease forwards;
}
.greeting {
  position: absolute;
  z-index: 3;
  color: white;
  opacity: 0.8;
  font-family: "Mavon Pro", sans-serif;
  font-size: 1vw;
  font-weight: 550;
}
.sun-container {
  position: absolute;
  display: flex;
  top: 50%;
  width: 100%;
  justify-content: center;
  -webkit-animation: rise 10s ease forwards;
          animation: rise 10s ease forwards;
}
.sun {
  z-index: 2;
  width: 100px;
  height: 100px;
  border-radius: 50%;
  background: linear-gradient(#ff0, #d00);
  box-shadow: 0 0 40px 6px #f20, 0 0 150px 10px #f33;
}
.sea {
  z-index: 4;
  position: absolute;
  background: radial-gradient(at 50% 0%, #013333, #002);
  top: 50%;
  opacity: 0.99;
  height:50%;
  width: 100%;
}
.sky {
  z-index: 1;
  position: absolute;
  opacity: 1;
  background: radial-gradient(at 50% 80%, #00564b, #013535, #0a1020, #090916);
  width: 100%;
  height: 50%;
  top: 0;

}
.flag-size {
  width: 40px;
  height:40px;
}
.light {
  z-index: 10;
  position: absolute;
  width: 100%;
  height: 100%;
  background: linear-gradient(to top, white, black);
  -webkit-animation: over 10s ease forwards;
          animation: over 10s ease forwards;
}
.dawn {
  z-index: 9;
  position: absolute;
  width: 100%;
  height: 100%;
  background: radial-gradient(at 50% 50%, pink, transparent);
  -webkit-animation: over 10s ease forwards;
          animation: over 10s ease forwards;
}

@-webkit-keyframes rise{
  0%{
    top: 50%;
  }
  100%{
    top: 25%;
  }
}

@keyframes rise{
  0%{
    top: 50%;
  }
  100%{
    top: 25%;
  }
}
@-webkit-keyframes rise2{
  0%{
    top: 50%;
  }
  100%{
    top: 43.5%;
  }
}
@keyframes rise2{
  0%{
    top: 50%;
  }
  100%{
    top: 43.5%;
  }
}
@-webkit-keyframes over{
  0%{
    opacity: 0;
  }
  100%{
    opacity: 0.1;
  }
}
@keyframes over{
  0%{
    opacity: 0;
  }
  100%{
    opacity: 0.1;
  }
}
.contents-flex-grid {
  display: grid;
  grid-template-columns: 1fr 11fr 1fr 3fr 2fr 7fr;
  grid-template-rows: 1fr 1fr 1fr 1fr;
  justify-items: center;
  align-items: center;
  width: 100%;
  height: 100%;
  margin: 50px;
}
.contents-text {
  color: #013535;
  font-size: 2vw;
  font-weight: 400;
  grid-column: 7;
}
.contents-title {
  color: #013535;
  grid-column: 2;
  grid-row: 1 / 5;
  font-size: 4vw;
  font-weight: 600;
  word-wrap: break-word;
}
.contents-icon {
  grid-column: 5;
  width: 4vw;
  height: 4vh;
  -webkit-filter: invert(15%) sepia(58%) saturate(800%) hue-rotate(134deg) brightness(96%) contrast(104%);
          filter: invert(15%) sepia(58%) saturate(800%) hue-rotate(134deg) brightness(96%) contrast(104%);
  
}
.contents-links {
  justify-self: left;
}
.contents-image {
  width: 100%;
  height: 100%;
}
#ContentsPage {
  background: radial-gradient(at 66% 50%, white, lightgrey);
}
#AboutMePage {
  background: radial-gradient(at 50% 50%, white, lightgrey);
}
#EducationPage {
  background: radial-gradient(at 50% 100%, white, lightgrey);
}
#WorkExperiencePage {
  background: radial-gradient(at 50% 0%, white, lightgrey);
}
